header {
    position: relative;
    height: 50px; }

nav {
    position: fixed;
    width: 100%;
    height: 50px;
    background-color: #212121;
    z-index: 5; }

.hamburger {
    display: none; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;
    &_link {
        color: #ffffff;
        font-size: 15px;
        font-weight: 300;
        font-style: normal;
        letter-spacing: normal;
        text-align: left;
        text-decoration: none;
        transition: .4s all;
        &:hover {
            color: #707070; }
        &:active {
            color: #de354c; } }
    &_item {
        border-right: 2px dotted black;
        padding-right: 46px;
        transition: color .2s linear;
        &:last-child {
            border-right: none; } } }

.subheader {
    position: absolute;
    top: 50px;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);
    &_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }
    &_official {
        margin-top: 6px;
        opacity: 0.5;
        color: #ffffff;
        font-size: 13px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        text-align: left;
        line-height: normal; }
    &_call {
        color: #ffffff;
        margin-top: 17px;
        font-size: 12px;
        font-weight: 300;
        font-style: normal;
        text-align: right;
        letter-spacing: normal; }
    &_phone {
        display: block;
        color: #ffffff;
        font-size: 22px;
        font-weight: 400;
        font-style: normal;
        text-align: right;
        text-transform: uppercase;
        text-decoration: none;
        line-height: 25px; }
    &_btn {
        margin-top: 19px;
        width: 172px;
        height: 46px;
        box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        text-transform: uppercase;
        @include btn_hover; } }

.promo {
    min-height: 800px;
    background: url('../img/main_bg.png') center (center / cover) no-repeat;
    padding: 150px 0 135px 0;
    &_header {
        margin-top: 67px;
        color: #ffffff;
        font-weight: 700;
        font-size: 60px;
        font-style: normal;
        letter-spacing: normal;
        line-height: 60px;
        text-align: center;
        text-transform: uppercase; }
    &_subheader {
        color: #ffffff;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        font-size: 36px;
        line-height: 36px;
        margin: 0;
        margin-top: 22px; }
    &_descr {
        color: #ffffff;
        font-size: 18px;
        font-weight: 300;
        line-height: 24px;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        line-height: normal;
        margin: 0;
        margin-top: 23px; }
    &_btn {
        display: block;
        margin: 188px auto 0 auto;
        width: 259px;
        height: 63px;
        box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
        border: 3px solid rgba(0, 0, 0, 0.07);
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        border: none;
        border-radius: 4px;
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        text-transform: uppercase;
        @include btn_hover; } }



.label {
    position: relative;
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
    &_white {
        background-color: #ffffff;
        color: #000; }
    &_map {
        background-color: #ffffff;
        color: #000; } }



.title {
    position: relative;
    margin-bottom: 0;
    margin-top: 32px;
    color: #222222;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
    &_white {
        color: #ffffff; } }

.subtitle {
    position: relative;
    margin-top: 5px;
    color: #222222;
    font-size: 17px;
    font-weight: 300;
    font-style: normal;
    letter-spacing: normal;
    text-align: center;
    z-index: 2;
    &_white {
        color: #ffffff; } }

.reasons {
    padding: 71px 0 65px 0;
    &_block {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 35px; }
    &_round {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 26px;
        width: 113px;
        height: 113px;
        background-color: #1eacc7;
        border-radius: 100%; }
    &_descr {
        width: 343px; }
    &_subtitle {
        line-height: 21px;
        color: #1eacc7;
        font-size: 17px;
        font-weight: 400; }
    &_text {
        color: #202020;
        font-size: 15px;
        font-weight: 300;
        line-height: 21px; } }

.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../img/bg_mob_ap.png') center (center / cover) no-repeat;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background: url('../img/mob_ap_1.png') center (center / cover) no-repeat; }
        &_2 {
            background: url('../img/mob_ap_2.png') center (center / cover) no-repeat; }
        &_3 {
            background: url('../img/mob_ap_3.png') center (center / cover) no-repeat; }
        &_4 {
            background: url('../img/mob_ap_4.png') center (center / cover) no-repeat; }
        &_5 {
            background: url('../img/mob_ap_5.png') center (center / cover) no-repeat; }
        &_6 {
            background: url('../img/mob_ap_6.png') center (center / cover) no-repeat; }
        &_subtitle {
            color: #ffffff;
            font-size: 24px;
            font-weight: 300;
            font-style: normal;
            letter-spacing: normal;
            line-height: 31px;
            text-align: center;
            text-transform: uppercase; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            width: 26px;
            height: 26px;
            background-color: #000000;
            margin: 0 auto;
            border-radius: 100%;
            cursor: pointer;
            &:before {
                content: '';
                display: block;
                width: 14px;
                height: 2px;
                background-color: $main-color; }
            &:after {
                content: '';
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                display: block;
                height: 14px;
                width: 2px;
                background-color: $main-color;
                transition: 0.3s all; }
            &:hover {
                &:before, &:after {
                    background-color: $second-color; } } } } }


.choice {
    display: {}
    min-height: 896px;
    padding: 57px 0 138px 0;
    background: url(../img/bg_uber.png) center (center / cover) no-repeat;
    &_img {
        position: relative;
        display: block;
        margin: 56px auto 0 auto;
        z-index: 2; }
    &_descr {
        color: #222222;
        font-size: 17px;
        font-weight: 300;
        font-style: normal;
        letter-spacing: normal;
        text-align: center; } }

.require {
    padding: 68px 0 84px 0;
    &_block {
        margin: 33px 0;
        &_nmb {
            margin-bottom: 0px; } }
    &_title {
        color: #1ca8c3;
        font-size: 22px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; }
    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;
        li {
            position: relative;
            font-size: 17px;
            font-weight: 300;
            line-height: 24px;
            margin-bottom: 10px;
            &:before {
                content: '';
                display: block;
                position: absolute;
                left: -32px;
                top: 3px;
                width: 21px;
                height: 20px;
                background: url('../icons/check_mark_blue.svg') center (center / cover) no-repeat; } } }
    .warning {
        .require_title {
            color: $second-color; }
        .require_list {
            li {
                &:before {
                    background: url('../icons/check_mark_red.svg') center (center / cover) no-repeat; } } } } }

.map {
    padding-top: 49px;
    min-height: 717px;
    background: url('../img/world_bg.png') center (center / cover) no-repeat; }

.map_link {
    position: relative;
    display: block;
    color: #1fbad6;
    font-size: 15px;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    padding-top: 486px;
    text-decoration: none; }

footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }
.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			i {
				color: #fff; } } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } } }
.xs-visible {
    display: none; }
