@media (max-width:1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; }
        &_subheader {
            font-size: 31px; }
        &_descr {
            font-size: 14px; } }
    .reasons {
        &_block {
            align-items: flex-start; }
        &_descr {
            width: 300px; } }
    .footer {
        &_mobile {
            width: 160px;
            display: flex;
            justify-content: start; }
        &_links {
            width: 350px; } }
    .mobile {
        &_item {
            width: 300px;
            &_subtitle {
                font-size: 21px; } } }
    .choice {
        &_img {
            width: 600px; } }
    .map {
        min-height: 800px; }
    .footer {
        &_links {
            text-align: center; } } }

@media (max-width:991px) {
    .menu {
        &_item {
            padding-right: 8px; }
        &_link {
            font-size: 10px; } }
    .subheader {
        &_logo {
            img {
                width: 100%; } }
        &_official {
            font-size: 11px; }
        &_phone {
            font-size: 18px; }
        &_btn {
            margin-top: 21px;
            height: 40px;
            width: 100%; } }
    .promo {
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 30px; } }
    .reasons {
        &_block {
            flex-direction: column;
            align-items: center; }
        &_round {
            margin-right: 0; }
        &_descr {
            width: 300px;
            margin-top: 20px; } }
    .mobile {
        &_item {
            width: 220px;
            &_subtitle {
                font-size: 18px; } } }
    .choice {
        min-height: 800px;
        &_img {
            width: 600px; } }
    .map {
        min-height: 650px; }
    .footer {
        &_links {
            text-align: center; }
        &_wrapper {
            flex-wrap: wrap; }
        &_wrapper > div {
            width: 100%;
            display: flex;
            justify-content: center;
            margin-bottom: 20px;
            &:nth-child(1) {
                width: 100%; }
            &:nth-child(3) {
                width: 100%;
                justify-content: center; } } } }

@media (max-width:767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        right: 0;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0,0,0, .8);
        transition: .2s all;
        &_active {
            left: 0; }
        &_item {
            padding-right: 0;
            border: none;
            margin: 10px 0; }
        &_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: .2s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            margin-top: 10px;
            color: #ffffff;
            font-size: 14px;
            font-weight: 400;
            text-transform: uppercase;
            text-align: center; }
        &_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto; } }
    .promo {
        &_header {
            line-height: 45px; } }
    .choice {
        min-height: 550px;
        padding: 57px 0;
        &_img {
            width: 500px; } }
    .map {
        min-height: 200px; }
    .footer {
        &_links {
            text-align: center; } } }

@media (max-width:575px) {
    header {
        height: 30px; }
    nav {
        height: 30px; }
    .subheader {
        height: 60px;
        top: 30px;
        &_official {
            display: none; }
        &_logo {
            margin-top: 15px; }
        &_phone {
            font-size: 12x;
            font-weight: 400;
            text-transform: uppercase;
            margin-top: 2px; }
        &_btn {
            width: 118px;
            height: 22px;
            font-size: 10px;
            margin-top: 4px; } }
    .promo {
        min-height: 400px;
        padding: 23px 0 35px 0;
        &_descr {
            display: none; }
        &_header {
            font-size: 30px;
            padding: 0 24px;
            line-height: 35px; }
        &_subheader {
            font-size: 25px;
            margin-top: 33px;
            padding: 0 10px;
            line-height: 30px; }
        &_btn {
            width: 210px;
            height: 50px;
            margin-top: 72px;
            font-size: 14px;
            text-align: center; } }
    .menu {
        width: 85%;
        &_item {
            width: 100%;
            text-align: center; }
        &_link {
            display: block; } }
    .reasons {
        padding: 22px 0 36px 0;
        &_block {
            margin-top: 26px; }
        &_round {
            width: 100px;
            height: 100px; }
        &_descr {
            width: 100%;
            margin-top: 27px; }
        &_subtitle {
            font-size: 16px; } }
    .choice {
        &_img {
            width: 290px; } }
    .map {
        min-height: 200px; }
    .footer {
        &_divider {
            width: 253px;
            margin: 0 auto;
            display: flex; }
        &_wrapper {
            padding-top: 22px; }
        &_wrapper > div {
            margin-bottom: 20px;
            &:nth-child(1) {
                order: 2; }
            &:nth-child(2) {
                order: 1; }
            &:nth-child(3) {
                order: 3; } }
        &_links {
            width: 290px;
            &_main {
                padding: 0;
                font-size: 14px; }
            &_sub {
                flex-wrap: wrap;
                justify-content: center; }
            a {
                margin: 0 7px; }
            &_lang {
                margin-top: 10px; } } }
    .title {
        font-size: 16px;
        margin-top: 15px;
        line-height: 16px; } }
